import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../../helper/function";
import { VariableService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import HistorySetVarComponent from "./HistorySetVarComponent";

export default function ModifyComponent(props: any) {
    const [name, setName] = useState('');
    const [initalValue, setInitalValue] = useState('');
    const [currentValue, setCurrentValue] = useState('');

    const [visibleHistory, setVisibleHistory] = useState(false);

    const { loadData, keyData, setKeyData, projectKey, isLoading, setIsLoading, visible, setVisible } = props;

    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            VariableService.GetDetail(projectKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setName(rs.data.name);
                        setInitalValue(rs.data.initalValue);
                        setCurrentValue(rs.data.currentValue);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyData, projectKey, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function handleOnChangeInitalValue(value: string) {
        setCurrentValue(value);
    }

    function handleSubmitForm() {
        let body = {
            name: name,
            initalValue: initalValue,
            currentValue: currentValue
        };

        setIsLoading(true);
        if (keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        VariableService.Add(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    HandleOnClose();
                    setVisible(false);
                    loadData();
                }
            })
            .finally(() => setIsLoading(false));
    }

    function Edit(body: any) {
        VariableService.GetDetail(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    VariableService.Edit(projectKey, keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function HandleOnClose() {
        setName('');
        setCurrentValue('');
        setInitalValue('');
        setVisible(false);
        if (setKeyData !== undefined) {
            setKeyData(undefined);
        }
    }

    function HandleDelete() {
        props.setIsLoading(true);
        VariableService.GetDetail(projectKey, keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    VariableService.Delete(projectKey, keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                HandleOnClose();
                                setVisible(false);
                                loadData();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function handleViewHistory() {
        setVisibleHistory(true);
    }

    return (
        <Drawer
            open={visible}
            onClose={HandleOnClose}
            title={keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"}>
            <Form
                onSubmitCapture={handleSubmitForm}
                layout="vertical">
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Input
                    label="Giá trị khởi tạo"
                    readOnly={keyData !== undefined}
                    onChange={handleOnChangeInitalValue}
                    value={initalValue}
                    setValue={setInitalValue} />

                <MyFormItem.Input
                    label="Giá trị hiện tại"
                    readOnly={keyData === undefined}
                    value={currentValue}
                    setValue={setCurrentValue} />

                <Row>
                    <Col>
                        <MyButton type="link" value="Lịch sử thay đổi" onClick={handleViewHistory}/>
                    </Col>
                </Row>
                <MyButton
                    htmlType="submit"
                    type="success"
                    isLoading={isLoading}
                    value={keyData === undefined ? "Thêm mới" : "Chỉnh sửa"} />

                <MyButton
                    type="danger"
                    onClick={HandleDelete}
                    disabled={keyData === undefined}
                    isLoading={isLoading}
                    value="Xóa" />
            </Form>

            <HistorySetVarComponent
                visible={visibleHistory}
                setVisible={setVisibleHistory} 
                keyData={keyData}
                projectKey={projectKey}/>
        </Drawer>
    );
}