import { Drawer } from "antd";
import { useCallback, useEffect, useState } from "react";
import { VariableService } from "../../../../../services/taskService";
import { Box } from "../../../../core/MyBox";
import { Text, TextStrong, TextWrap } from "../../../../core/Text";
import { UIFormatDateTimeVNComponent } from "../../../../core/UI_FormatComponent";

export default function HistorySetVarComponent(props: any) {
    const { visible, setVisible, keyData, projectKey } = props;
    const [data, setData] = useState<any>([]);

    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined && visible !== false) {
            VariableService.GetHistory(projectKey, keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setData(rs.data.pagedData);
                    }
                })
        }
    }, [keyData, projectKey, visible]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function onClose() {
        setVisible(false);
    }

    function renderData() {
        return data.map((data: any, index: number) =>
            <Box key={index}>
                <Text>
                    <TextStrong value="Giá trị: " />
                    <TextWrap>{data.value}</TextWrap>
                </Text>
                <Text>
                    <TextStrong value="Thời gian thay đổi: " />
                    <UIFormatDateTimeVNComponent value={data.updatedAt} />
                </Text>
            </Box>
        )
    }
    return (
        <div>
            <Drawer
                open={visible}
                onClose={onClose}>
                {renderData()}
            </Drawer>
        </div>
    );
}